import React from 'react';
import Card from './Card';

const CardList = ({recipies}) =>{
    const CardArray = recipies.map((value,index) => {
        return (<Card key={index} image = {recipies[index].image} name = {recipies[index].name} descripton = {recipies[index].description}/>);
    });
    
    return (
        <div>
            {CardArray}
        </div>
    );
}
export default CardList;