import React, { Component } from 'react';
import CardList from '../components/CardList';
import SearchBox from '../components/SearchBox';
import ScrollBar from '../components/ScrollBar';
//import ErrorBoundry from '../components/ErrorBoundry';
import './App.css';

class App extends Component {
  constructor() {
    super();
    this.state = {
      recipies: [],
      searchField: ''
    }
  }
  onSearchChange = (event) => {
    this.setState({ searchField: event.target.value })
  }
  componentDidMount() {
    fetch('http://starlord.hackerearth.com/recipe')
      .then(response => response.json())
      .then(recipe => this.setState({ recipies: recipe })
      );
  }
  render() {
    const filterRecipe = this.state.recipies.filter(recipe => {
      return recipe.name.toLowerCase().includes(this.state.searchField.toLowerCase());
    });
    return (
      <div className='tc'>
        <h1 className='f1'>Pizza and Nodles</h1>
        <SearchBox searchChange={this.onSearchChange} />
        {/*<ErrorBoundry>*/}
        <ScrollBar >
          <CardList recipies={filterRecipe} />
        </ScrollBar>
        {/*</ErrorBoundry>*/}
      </div>
    );
  }
}

export default App;
